<template>
  <div class="setting">
    <el-form
      :inline="true"
      :model="setting"
      @submit.native.prevent="onSubmit"
      class="form-search"
      label-width="120px"
      v-loading="loading"
    >
      <div class="searchs">
        <el-row :gutter="24">
          <el-col>
            <el-form-item label="合同书名">
              <el-input
                v-model="setting.title"
                size="small"
                placeholder="请输入内容"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="乙方姓名">
              <el-input
                v-model="setting.name2"
                size="small"
                placeholder="请输入内容"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="丙方姓名">
              <el-input
                v-model="setting.name3"
                size="small"
                placeholder="请输入内容"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="合同内容">
              <el-input
                size="small"
                :rows="10"
                :cols="20"
                type="textarea"
                v-model="setting.contract_content"
                placeholder="请输入内容"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="乙方印章">
              <el-upload
                :limit="1"
                :action="'/api/api/upload?ssid=' + ssid()"
                :on-success="fileSuccess1"
                :file-list="fileList1"
                list-type="picture"
              >
                <el-button size="small" type="infor">点击上传</el-button>
                <div slot="tip" style="color: red" class="el-upload__tip">
                  尺寸:宽500px 高:500px 格式:png、jpg、jpeg、gif
                </div>
              </el-upload>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="丙方印章">
              <el-upload
                :limit="1"
                :action="'/api/api/upload?ssid=' + ssid()"
                :on-success="fileSuccess2"
                :file-list="fileList2"
                list-type="picture"
              >
                <el-button size="small" type="infor">点击上传</el-button>
                <div slot="tip" style="color: red" class="el-upload__tip">
                  尺寸:宽500px 高:500px 格式:png、jpg、jpeg、gif
                </div>
              </el-upload>
            </el-form-item>
          </el-col>
        </el-row>
      </div>
      <el-col>
        <el-form-item>
          <el-button type="primary" size="small" native-type="submit"
            >保存</el-button
          >
        </el-form-item>
      </el-col>
    </el-form>
  </div>
</template>
<script>
import { Message } from 'element-ui'
import Fetch from '../../utils/fetch'

export default {
  data() {
    return {
      loading: false,
      fileList1: [],
      fileList2: [],
      setting: {},
    }
  },
  mounted() {
    this.loading = true
    Fetch('/sys/config/info', {
      type: 'contract_dq',
    })
      .then(r => {
        this.setting = r.data
        this.loading = false
        this.fileList1 = [
          {
            name: r.data.contract_image1,
            url: r.data.contract_image1,
          },
        ]
        this.fileList2 = [
          {
            name: r.data.contract_image2,
            url: r.data.contract_image2,
          },
        ]
      })
      .catch(() => (this.loading = false))
  },
  methods: {
    onSubmit() {
      this.loading = true
      Fetch('/sys/config/update', {
        type: 'contract_dq',
        ...this.setting,
      })
        .then(() => {
          Message.success('更新成功')
          this.loading = false
        })
        .catch(() => {
          Message.error('更新失败')
          this.loading = false
        })
    },
    ssid() {
      return localStorage.getItem('ssid')
    },
    fileSuccess1(res) {
      this.setting.contract_image1 = res.data.file
    },
    fileSuccess2(res) {
      this.setting.contract_image2 = res.data.file
    },
  },
}
</script>
<style lang="less" scoped>
.setting {
  > div {
    padding: 14px 14px 14px 15px;
  }

  .searchs {
    background-color: #f2f2f2;
    border-left: 5px solid #1aa094;
    color: #606266;
    font-size: 16px;
  }

  /deep/ .el-input__inner {
    width: 300px;
  }

  /deep/ .el-textarea {
    width: 300px;
  }
}
</style>
